.bootstrap-datetimepicker-widget {
	&.dropdown-menu.timepicker-sbs {
		.datepicker-days, .datepicker-months, .datepicker-years, .datepicker-decades {border-bottom:dashed 1px rgba(0,0,0,.1);}
	}

	.datepicker-days table {
		tr {
			td {font-weight:bold;
				&.disabled, &.new {font-weight:normal;color:rgba(0,0,0,.5);}
				&.weekend:first-of-type {color:rgba(255, 80, 80, 1);}
				&.weekend:last-of-type {color:rgba(16, 62, 138, 1);}
				&.disabled.weekend:first-of-type, &.new.weekend:first-of-type {color:rgba(255, 80, 80, .5);}
				&.disabled.weekend:last-of-type, &.new.weekend:last-of-type {color:rgba(16, 62, 138, .5);}

			}
		}
		td.active.weekend, td.active.weekend:hover {color:#fff !important;}
	}

	table {border:none !important;
		td, th {border-radius: 0;}
		td {
			span {border-radius:0;
				&.active {background-color:$primary-color;}
			}
			&.today:before {border:none;width:4px;height:4px;background:$primary-color;border-radius:2px;}
			&.active, &.active:hover {background-color:$primary-color;}
		}
	}

	.prev {position:relative;
		.arrow-left {width:100%; height:33px; position:absolute; left:0; top:0;
			&:after {content:'';@include sprite($arrow-left-on);position:absolute;left:50%;top:50%;@include transform(translate3d(-50%, -50%, 0));}
		}
		&.disabled {
			.arrow-left:after {@include sprite($arrow-left);}
		}
	}
	.next {position:relative;
		.arrow-right { width:100%; height:33px; position:absolute; left:0; top:0;
			&:after { content:''; @include sprite($arrow-right-on); position:absolute; left:50%; top:50%; @include transform(translate3d(-50%, -50%, 0)); }
		}
		&.disabled {
			.arrow-right:after {@include sprite($arrow-right);}
		}
	}
	.timepicker {
		table {
			td {height:42px;line-height:42px;padding:0 5px;
				span {height:40px;line-height:40px;margin:0;}
			}
		}
		a[data-action] {padding:0;}
		.timepicker-picker {
			td { position: relative;
				a { display: block; width: 100%; height: 64px; position: absolute; top: 0; left: 0; border-radius: 0;
					&:hover { background: #eee; }
					.arrow-top { @include sprite($arrow-top-on); position: absolute; left: 50%; top: 50%; @include transform(translate3d(-50%, -50%, 0));
						&:hover { background-color: transparent !important; }
					}
					.arrow-bottom { @include sprite($arrow-bottom-on); position: absolute; left: 50%; top: 50%; @include transform(translate3d(-50%, -50%, 0));
						&:hover { background-color: transparent !important; }
					}
				}
				.timepicker-hour, .timepicker-minute { display: block; width: 100%; height: 74px; position: absolute; top: 0; left: 0; }
			}
		}
		.timepicker-hours td {font-weight:bold;}
		.timepicker-minutes td {font-weight:bold;}
	}

	.add-on {
		ul {margin:0 4px;
			li {width:33.333%;float:left;padding:0 2px;margin-top:4px;
				button {display:inline-block;margin:0;width:100%;height:26px;white-space:nowrap;font-size:11px;background:none;border:solid 1px #ccc;
					&:hover, &:active {border:solid 1px $primary-color;}
				}
			}
			&:after {content:'';clear:both;float:none;display:block;}
		}

	}

	.picker-switch.accordion-toggle {float:none !important;margin:0;display:block;background:#fff;padding-top:4px;padding-bottom:4px;
		a[data-action] {display:block;height:26px;padding:0;margin:0 6px 0 6px;background:$primary-color;
			span {height:auto;line-height:1;
				&:before {content:'확인';font-size:12px;color:#fff;}
				&:hover {background:none;}
			}

			&:hover, &:active {border:solid 1px $primary-color;}
		}
	}

	@include mq(768px) {
		&.dropdown-menu.timepicker-sbs {width:27em;
			.timepicker-picker {margin-left:-25px;}
			.timepicker-hours {margin-left:-25px;}
			.timepicker-minutes {margin-left:-25px;}
			.datepicker-days, .datepicker-months, .datepicker-years, .datepicker-decades {
				border-bottom:none;
				border-right:dashed 1px rgba(0,0,0,.1);
			}
		}
		.timepicker {
			.timepicker-picker {padding-top:20px;
				td {height: 64px; line-height: 64px;
					.timepicker-hour, .timepicker-minute {line-height: 74px;}
				}
			}
			table {
				td {height:54px;line-height:54px;padding:5px 0;
					span {height:54px;line-height:54px;}
				}
			}
			a[data-action] {padding:6px 0;}
		}
		.timepicker-hour, .timepicker-minute, .timepicker-second {width:35px;}
		.add-on {margin-right:0;margin-left:0;
			ul {
				li {width:16.666%;}
			}
		}
	}
}